<template>
  <div class="hs-container">
    <div id="map" style="width:100%;height:100%;"></div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  mounted() {
    this._initDetails()
  },
  data() {
    return {
      map: undefined,
      details: {
        Latitude: null,
        Longitude: null,
        Content: null
      }
    }
  },
  methods: {
    /** 初始化details数据 */
    _initDetails() {
      this.details.Latitude = this.$route.query.Latitude
      this.details.Longitude = this.$route.query.Longitude
      this.details.Content = this.$route.query.Content
      this._initMap()
    },
    /** 初始化地图 */
    _initMap() {
      console.log('地图')
      let mapOptions = {
        center: {lat: Number(this.details.Latitude), lng: Number(this.details.Longitude)}, // 地图的中心点
        zoom: 15, // 地图缩放比例
        // mapTypeId: mapType, // 指定地图展示类型：卫星图像、普通道路
        scrollwheel: true, // 是否允许滚轮滑动进行缩放
        disableDefaultUI: true
      }
      console.log('window: ', window)
      this.map = new window.google.maps.Map(document.getElementById('map'), mapOptions)
      let markerStart = new window.google.maps.Marker({
        position: mapOptions.center
        // icon: '' 自定义图标
      })
      markerStart.setMap(this.map)
      let infowindow = new window.google.maps.InfoWindow({ content: this.details.Content })
      markerStart.addListener('click', function() { infowindow.open(this.map, markerStart) })
      infowindow.open(this.map, markerStart)
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  position absolute
  width 100%
  height 100%
  top 0
  left 0
  background-color $color_white
  z-index 10
</style>



